  z-index: 2;
}

.card__title {
  position: relative;
  z-index: 3;
  text-align: center;
  margin-bottom: 140px;
}

.card__wrap {
  position: relative;
  max-width: 100%;
  max-height: 100%;
  z-index: 3;
  padding: 18px 31px;
  background-repeat: no-repeat;
  background-size: cover;
}

.card--green {
  background-color: #12472f;
  background-position: -70% 50%;
}

.card--brown {
  background-color: #2f2f2f;
  background-position: 35% 50%;
}

.card--yellow {
  background-color: #340900;
  background-position: 20% 50%;
}

.price-list {
