  z-index: 3;
}

.price-list__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Roboto";
  font-weight: 500;
}

.price-list__item:not(:last-child) {
  margin-bottom: 2px;
}

.price-list__item:first-child {
  margin-bottom: 8px;
}

.price-list__title:first-child {
  margin-right: 15px;
}

.price-list__col {
  color: rgba(255, 255, 255, 0.25);
  font-size: 14px;
}

.price-list__col:first-child {
  margin-right: 15px;
}

.price-list__input-title {
  color: #ffffff;
  margin-right: 15px;
}

@media only screen and (max-width: 767px) {
  .price-list__input-title {
    font-size: 14px;
  }
}

.price-list__col--mod {
  font-size: 16px;
  color: #11c535;
}

@media only screen and (max-width: 767px) {
  .price-list__col--mod {
