  }
}

.select {
  width: 100%;
}

@media only screen and (max-width: 767px) {
  .select {
    max-width: 270px;
  }
}

.select .select2 .select2-selection__rendered {
  font-size: 16px;
  font-weight: 200;
  line-height: 16px;
  text-transform: uppercase;
  color: #ffffff;
}

.select2-container--default .select2-selection--multiple,
.select2-container--default .select2-selection--single {
  background-color: transparent;
}

.select2-container .select2-selection--single {
  height: 52px;
  padding: 18px 0 18px;
  border: 1px solid #54e9ff;
  border-radius: 4px;
}

.select2-container .select2-selection--single .select2-selection__rendered {
  padding: 0 35px 0 16px;
}

.select2-container--default.select2-container--open.select2-container--below .select2-selection--multiple,
.select2-container--default.select2-container--open.select2-container--below .select2-selection--single {
  border-radius: 4px;
}

.select2-dropdown {
  margin-top: 10px;
  background-color: #142027;
  border: transparent;
}

.select2-container--open .select2-dropdown--below {
  border-radius: 4px;
}

.select2-container--default .select2-results__option--highlighted[aria-selected],
.select2-container--default .select2-results__option[aria-selected=true] {
  background-color: transparent;
  color: #ffffff;
}

.select2-results {
  padding: 10px 16px;
  border-radius: 4px;
  border: 1px solid #54e9ff;
}

.select2-results__option {
  padding: 6px 0;
  color: #ffffff;
  font-family: "Roboto";
  font-weight: 200;
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
  height: 52px;
  width: 30px;
}

.select2-container--default .select2-selection--single .select2-selection__arrow b {
  border-style: none;
  position: absolute;
  background-image: url(../img/arrow.svg);
  background-repeat: no-repeat;
  top: 50%;
  left: 50%;
  width: 10px;
  height: 10px;
  transform-origin: 50% 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
}

.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
  transform: rotate(180deg);
  top: 50%;
  left: 50%;
  width: 10px;
  height: 10px;
  margin-top: -4px;
  transform-origin: 50% 50%;
  transform: translate(-50%, -50%) rotate(180deg);
}

.toggler {
  position: relative;
  top: -8px;
  padding-left: 40px;
  cursor: pointer;
  user-select: none;
}

.toggler input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.toggler input:checked ~ .toggler__custom:before {
  box-shadow: inset 0 4px 8px rgba(0, 0, 0, 0.2);
  background-color: #babbbb;
}

.toggler input:checked ~ .toggler__custom:after {
  transform: translateX(23px);
}

.toggler__custom:before, .toggler__custom:after {
  position: absolute;
  content: '';
}

.toggler__custom:before {
  left: 0;
  top: 0;
  width: 40px;
  height: 15px;
  box-shadow: inset 0 4px 8px rgba(0, 0, 0, 0.2);
  background-color: #01ade6;
  outline: none;
  border-radius: 20px;
  transition: background-color .3s, box-shadow .3s;
}

.toggler__custom:after {
  width: 24px;
  height: 24px;
  background-image: url(../img/checkbox.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  top: -4px;
  left: -4px;
  transition: transform .3s;
  transform: translateX(0);
}

